/*==========  Desktop First  ==========*/
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

*:hover,
*:focus {
  outline: 0;
}

html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-width: 320px;
  position: relative;
  font-family: "TTNorms-Regular", sans-serif;
  font-size: 14px;
  line-height: 1.2;
  overflow-x: hidden;
  min-height: 100vh;
  padding: 0;
  color: #424242;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-anchor: none;
  background: #F2F2F2;
  caret-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

input,
button,
textarea,
select {
