  color: #7A7A7A;
  border-color: transparent;
  background-color: rgba(254, 71, 25, 0.1);
}
.checkBlock .custom-control-input:not(:checked) ~ .custom-control-label {
  background-color: #F2F2F2;
  border-color: #F2F2F2;
