.burger {
  position: relative;
  width: 48px;
  height: 51px;
  display: none;
  border: none;
  padding: 14px;
  border-radius: 12px;
  background-color: #ffffff;
  transition: transform 0.45s ease, position 0.35s ease;
  cursor: pointer;
}
.burger__line {
  position: absolute;
  left: 14px;
  top: 50%;
  width: 20px;
  height: 3px;
  border-radius: 3px;
  background-color: #252525;
  transform: translateY(-50%);
  transition: opacity 0.2s ease-in-out;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 14px;
  width: 20px;
  height: 3px;
  border-radius: 3px;
  background-color: #252525;
  transition: transform 0.2s ease-in-out, top 0.2s ease-in-out, bottom 0.2s ease-in-out;
}
.burger::before {
  top: 18px;
}
.burger::after {
  bottom: 18px;
}
.burger--active {
  transform: rotate(0deg);
  margin-left: 0;
}
.burger--active .burger__line {
  display: none;
}
.burger--active::before {
  width: 21px;
  transition: position 1s ease-in-out, top 0.6s ease-in-out, right 0.6s ease-in-out;
  transform: rotate(45deg);
  top: 24px;
  left: 13px;
  opacity: 1;
}
.burger--active::after {
  width: 21px;
  transition: position 1s ease-in-out, top 0.6s ease-in-out, bottom 0.6s ease-in-out;
  transform: rotate(-45deg);
  bottom: 24px;
  left: 13px;
  opacity: 1;
}
